.footer{     
      background-color: var(--color-primary);
      padding: 3rem 0;
      text-align: center;
      font-size: 0.9rem;
      margin-top: 7rem;
}

.footer a{
      color: var(--color-bg);
}

.footer__logo{
      font-size: 2rem;
      font-weight: 500;
      margin-bottom: 2rem;
      display: inline-block;
      width: 90%;
      height: auto;
}

.permalinks{
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
      gap: 2rem;
      margin: 0 auto 3rem;
}

.footer__socials{
      display: flex;
      justify-content: center;
      gap: 1rem;
      margin-bottom: 4rem;
}

.footer__socials a{
      color: var(--color-bg);
      border: 10px solid transparent;
      font-size: 1.5rem;
      transition: var(--transition);
}

.footer__socials a:hover{
      color: var(--color-     );
}

.footer__copyright{
      margin-bottom: 4rem;
      color: var(--color-bg)
}

.footer-btn{
      width: 200%;
      height: auto;
}



/* ======================= MEDIA QUERIES (MEDIUM DEVICES - TABLETS)=====================*/
@media screen and (max-width: 1024px) {
      .permalinks{
            flex-direction: column;
            gap: 1.5rem;
      }

      .footer__socials{
            margin-bottom: 2.6rem;
      }
}